







import Vue from "vue";
import { Component } from "vue-property-decorator";

@Component
export default class Types extends Vue {
  get type() {
    return this.$store.state.type;
  }
  selectType(type: string) {
    if (type !== "-" && type !== "+") {
      throw new Error("type is unknown");
    }
    this.$store.commit("selectType", type);
  }
}
